p {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

address {
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
}

article {
  background: none;
  border: 0;
  outline: 0;
  margin: 0;
  padding: 0;
  font-size: 100%;
  display: block;
}

:root {
  --title-font-family: "Poppins", sans-serif;
  --primary-font-family: "Poppins", sans-serif;
  --gallery-font-family: "Roboto", sans-serif;
  --light-bg-color: #fcfcfc;
  --dark-bg-color: #202025;
  --white-color: #fff;
  --black-color: #000;
  --medium-grey-color: #5f6775;
  --title-light-color: #202025;
  --title-dark-color: #fcfcfc;
  --filter-letter-light-color: #5f6775;
  --filter-letter-dark-color: #5f6775;
  --modal-text-light-color: #5f6775;
  --modal-text-dark-color: #fcfcfc;
  --modal-title--black: #000917;
  --btn-bg-color: #fe7031;
  --btn-inmodal-bg-color: #fd5103;
  --btn-text-color: #fd5103;
  --btn-outline-color: #ff8650;
  --pretitle-hero-color: #fd5103;
  --accent-color: #fd5103;
  --btn-hover-color: #fd5103;
  --card-item: 1;
  --card-gap: 20px;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

html {
  scroll-behavior: smooth;
}

body {
  font-style: normal;
  font-weight: 500;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 22px;
  font-weight: 600;
  line-height: 1.91;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.card-set {
  gap: var(--card-gap);
  flex-wrap: wrap;
  display: flex;
}

.card-set-item {
  flex-basis: calc((100% - (var(--card-item)  - 1) * var(--card-gap)) / var(--card-item));
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media (min-width: 320px) {
  .container {
    width: 320px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
